.standingsCard {
    width: 33%;
    float: left;
    box-sizing: border-box;
    padding: 0px 20px 0px 20px;
    min-width: 245px;
}

.standingsCard ol {
    line-height: 1.5em;
    font-size: 1.2em;
}

.standingsCard .name {
    width: 60px;
    display: inline-block;
}

.standingsCard .record {
    width: 35px;
    display: inline-block;
}

.standingsCard .spacing {
    width: 100px;
    display: inline-block;
}