.App {
  text-align: center;
  width: 100%;
  margin: auto;
  max-width: 1500px;
  min-width: 600px;
  background: #fff;
  min-height: 500px;
  border-left: 1px solid black;
  border-bottom: 1px solid black;
  border-right: 1px solid black;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.header {
  background: #0000aa;
}

.header a {
  color: #fff;
  text-decoration: none;
}

#header-menu {
  max-width: 1500px;
  width: 100%;
  margin: auto;
  list-style-type: none;
  font-size: 2rem;
  padding: 0px;
}

#header-menu li {
  display: inline;
  padding: 10px;
}